#imprint {
  display: flex;
  flex-direction: column;
  margin-bottom: 50px;
  font-size: 14px;

  .header {
    flex: 1;
    padding: 40px 0;
    text-align: center;
    color: white;
    background-color: rgb(0, 143, 213);

    h1 {
      font-family: "VW Headline OT Black", "Arial", sans-serif;
      font-size: 32px;
    }
  }

  /* content */
  .content {
    flex: 1;
  }

  .intro {
    width: 50%;
    margin: 0 auto 10px auto;

    h1 {
      font-family: "VW Headline OT", "Arial", sans-serif;
      font-size: 28px;
      margin-bottom: 10px;
    }

    p {
      font-size: 14px;
    }
  }

  /* Sections - Block Elements */
  .block {
    width: 50%;
    margin: 25px auto 0 auto;

    h2 {
      font-family: "VW Headline OT", "Arial", sans-serif;
      font-size: 15px;
      margin-top: 40px;
      margin-bottom: 5px;
      line-height: 0.5;
    }

    p {
      font-size: 14px;
      margin: 0;
    }
  }
}
